<template>
	<view class="box">
		<view class="tab-info">
			<view class="tab-li" :class="{'active':identityType=='PERSONAL'}" @tap="chooseType('PERSONAL')">个人身份</view>
			<view class="tab-li" :class="{'active':identityType=='ENTERPRISE'}" @tap="chooseType('ENTERPRISE')">企业身份</view>
		</view>
		<view class="info-li" v-if="identityType=='PERSONAL'">
			<view class="left">
				<view class="iconfont iconwode"></view>
				<view class="title">
					昵称<text class="text-red">*</text>
				</view>
			</view>
			<input class="info-li-input" v-model="customerName" type="text" placeholder="显示在广告中的发布者名称">
		</view>
		<view class="info-li" v-if="identityType=='PERSONAL'">
			<view class="left">
				<view class="iconfont iconwode"></view>
				<view class="title">
					真实姓名<text class="text-red">*</text>
				</view>
			</view>
			<input class="info-li-input" v-model="realName" type="text" placeholder="请填写真实姓名">
		</view>
		<view class="info-li" v-if="identityType=='ENTERPRISE'">
			<view class="left">
				<view class="iconfont iconwode"></view>
				<view class="title">
					公司名称<text class="text-red">*</text>
				</view>
			</view>
			<input class="info-li-input" v-model="enterpriseName" type="text" placeholder="显示在广告中的发布者名称">
		</view>
		<view class="info-li" v-if="identityType=='ENTERPRISE'">
			<view class="left">
				<view class="iconfont iconwode"></view>
				<view class="title">
					法人姓名<text class="text-red">*</text>
				</view>
			</view>
			<input class="info-li-input" v-model="legalPerson" type="text" placeholder="请输入法人姓名">
		</view>
		<view class="info-li">
			<view class="left">
				<view class="iconfont iconshenfenzheng"></view>
				<view class="title">
					身份证号码<text class="text-red">*</text>
				</view>
			</view>
			<input class="info-li-input" maxlength="18" v-model="idCardCode" type="text" placeholder="请输入真实的身份证号码">
		</view>
		<view class="info-li info-photo-li">
			<view class="card-title">
				请上传有效的二代身份证
			</view>
			<view class="photo_list">
				<view class="photo_li" v-if="!idCardFront" @tap="chooseImage('idCardFront')">
					<view class="photo_default">
						<image lazy-load class="default_icon" src="../../static/images/photo.png" mode="aspectFill"></image>
						<view class="default_text">
							身份证正面照片
						</view>
					</view>
				</view>
				<view class="photo_li " v-else>
					<image lazy-load class="default_img" :src="idCardFront" mode="aspectFill"></image>
					<view class="iconfont iconcuowu" @tap="closeImg('idCardFront')"></view>
				</view>

				<view class="photo_li" v-if="!idCardBack" @tap="chooseImage('idCardBack')">
					<view class="photo_default">
						<image lazy-load class="default_icon" src="../../static/images/photo.png" mode="aspectFill"></image>
						<view class="default_text">
							身份证反面照片
						</view>
					</view>
				</view>
				<view class="photo_li " v-else @tap="closeImg('idCardBack')">
					<image lazy-load class="default_img" :src="idCardBack" mode="aspectFill"></image>
					<view class="iconfont iconcuowu"></view>
				</view>

				<view class="photo_li" v-if="!holdIdCard" @tap="chooseImage('holdIdCard')">
					<view class="photo_default">
						<template>
							<image lazy-load class="default_icon" src="../../static/images/photo.png" mode="aspectFill"></image>
							<view class="default_text">
								手持身份证照片
							</view>
						</template>
					</view>
				</view>
				<view class="photo_li " v-else @tap="closeImg('holdIdCard')">
					<image lazy-load class="default_img" :src="holdIdCard" mode="aspectFill"></image>
					<view class="iconfont iconcuowu"></view>
				</view>

			</view>
		</view>
		<view class="info-li info-photo-li" v-if="identityType=='ENTERPRISE'">
			<view class="card-title">
				上传营业执照照片
			</view>
			<view class="photo_list">
				<view class="photo_li" v-if="!businessLicense" @tap="chooseImage('businessLicense')">
					<view class="photo_default">
						<image lazy-load class="default_icon" src="../../static/images/photo.png" mode="aspectFill"></image>
						<view class="default_text">
							营业执照照片
						</view>
					</view>
				</view>
				<view class="photo_li " v-else>
					<image lazy-load class="default_img" :src="businessLicense" mode="aspectFill"></image>
					<view class="iconfont iconcuowu" @tap="closeImg('businessLicense')"></view>
				</view>
		
			</view>
		</view>
		<view class="info-li">
			<view class="left">
				<view class="iconfont iconxingbie"></view>
				<view class="title">
					性别<text class="text-red">*</text>
				</view>
			</view>
			<view class="right">
				<radio-group name="gender" @change="radioChange">
					<label class="gender-l">
						<radio value="1" :checked='gender=="MALE"' /><text>男</text>
					</label>
					<label>
						<radio value="2" :checked='gender=="FEMALE"' /><text>女</text>
					</label>
				</radio-group>
			</view>
		</view>
		<view class="info-li">
			<view class="left">
				<view class="iconfont iconshoujihao"></view>
				<view class="title">
					手机号<text class="text-red">*</text>
				</view>
			</view>
			<input class="info-li-input" v-model="mobile" maxlength="11" type="text" placeholder="请输入真实的手机号">
		</view>
		<view class="info-li">
			<view class="left">
				<view class="iconfont iconyanzhengma"></view>
				<view class="title">
					验证码<text class="text-red">*</text>
				</view>
			</view>
			<input class="info-li-input" v-model="verificationCode" type="text" placeholder="请输入验证码">
			<view class="yzm-btn border-btn" @tap="getPhoneCode">
				{{yzm_text}}
			</view>
		</view>
		<view class="info-li">
			<view class="left">
				<view class="iconfont iconyouxiang"></view>
				<view class="title">
					邮箱
				</view>
			</view>
			<input class="info-li-input" v-model="email" type="text" placeholder="选填">
		</view>
		<button class="no-btn next-btn" @tap='save' :loading="loading">确认提交</button>

	</view>
</template>

<script>
	let t = 60;
	let cut = null;
	import {
		mapState
	} from 'vuex';
	let is_second=false;
	export default {
		data() {
			return {
				loading:false,
				api_url: this.$webconfig.api_url,
				yzm_text:"获取验证码",
				"customerName":"", //昵称  
				"customerSeqId":"", //会员id
				"realName":"", //真实姓名
				"gender":"MALE", //性别 
				"idCardBack":"", //身份证背面 
				"idCardFront":"", //身份证正面
				"enterpriseName":"", //企业名称
				"businessLicense":"",//营业执照
				"legalPerson":"",// 法人
				"mobile":"", //手机 
				"identityType":"PERSONAL", //身份类型(个人或企业) ['PERSONAL', 'ENTERPRISE']
				"holdIdCard":"",//手持身份证照片
				"idCardCode":"",//身份证号码
				"email":"",//邮箱
				"verificationCode":"",// 验证码
				seqId:"",//编辑

			}
		},
		onLoad(option) {
			this.customerSeqId=this.$common.getLocalSync('customerSeqId');
			is_second=option.type?true:false;
			
			if(is_second){
				uni.setNavigationBarTitle({
					title:"编辑"
				})
				let identity=this.$common.getLocalSync('identity');
				this.customerName=identity.customerName; //昵称
				this.customerSeqId=identity.customerSeqId; //会员id
				this.realName=identity.realName; //真实姓名
				this.gender=identity.gender; //性别 
				this.idCardBack=identity.idCardBack; //身份证背面 
				this.idCardFront=identity.idCardFront; //身份证正面
				this.enterpriseName=identity.enterpriseName; //企业名称
				this.businessLicense=identity.businessLicense;//营业执照
				this.legalPerson=identity.legalPerson;// 法人
				this.mobile=identity.mobile; //手机 
				this.identityType=identity.identityType; //身份类型(个人或企业) 
				this.holdIdCard=identity.holdIdCard;//手持身份证照片
				this.idCardCode=identity.idCardCode;//身份证号码
				this.email=identity.email;//邮箱
				this.seqId=identity.seqId
			}
		},
		
		methods: {
			getPhoneCode() {
				let self = this;
				let phonereg = /^1[3456789]\d{9}$/
				if (!(phonereg.test(this.mobile))) {
					this.$box.toast('请填写正确的手机号码', 'none');
					return;
				}
				if (cut) {
					return
				}
				this.$api.get('/wechat/email/getSmsCode', {
					params: {
						mobile: this.mobile
					}
				}).then(res => {
					if (res.code == 200) {
						this.$box.toast(res.msg);
						cut = setInterval(function() {
							self.cutTime()
						}, 1000)
					}
				})


			},
			cutTime() {
				let self = this;
				if (t > 0) {
					t--;
					self.yzm_text = t + "S后重试"
				} else {
					t = 60;
					self.yzm_text = "获取验证码"
					clearInterval(cut)
					cut=null
				}
			},
			save() {
				if(this.loading)return;
				this.loading=true;
				let url="/o2oIdentityManage/save";
				let obj={
					"customerName":this.customerName, //昵称
					"customerSeqId":this.customerSeqId, //会员id
					"realName":this.realName, //真实姓名
					"gender":this.gender, //性别 
					"idCardBack":this.idCardBack, //身份证背面 
					"idCardFront":this.idCardFront, //身份证正面
					"enterpriseName":this.enterpriseName, //企业名称
					"businessLicense":this.businessLicense,//营业执照
					"legalPerson":this.legalPerson,// 法人
					"mobile":this.mobile, //手机 
					"identityType":this.identityType, //身份类型(个人或企业) 
					"holdIdCard":this.holdIdCard,//手持身份证照片
					"idCardCode":this.idCardCode,//身份证号码
					"email":this.email,//邮箱
					"verificationCode":this.verificationCode,
				}
				if(is_second){
					url="/o2oIdentityManage/update";
					obj.seqId=this.seqId
				}
				
				this.$api.post(url, obj).then(res => {
					this.loading=false;
					if(res.code==200){
						this.$box.toast('提交成功')
						this.$common.removeLocal('identity')
					}
					
				}).catch(err=>{
					this.loading=false;
					console.log(err);
				})
			},


			// 删除图片
			closeImg(item) {
				this[item] = ""
			},
			// 上传图片
			chooseImage(item) {
				let self = this;
				uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
					sourceType: ['album'],
					success: (res) => {
						var imageSrc = res.tempFilePaths[0]
						uni.uploadFile({
							url: self.api_url + '/sys/appeal/imgUpload',
							filePath: imageSrc,
							fileType: 'image',
							header: {
								Authorization: self.$common.getLocalSync('token')
							},
							name: 'file',
							success: (res) => {
								if (res.statusCode == 200) {
									let data = JSON.parse(res.data)
									if (data.code == 200) {
										self[item] = data.data.viewUrl;
									} else {
										this.$box.toast('上传失败', 'none')
									}
								} else {
									this.$box.toast('上传失败', 'none')
								}

							},
							fail: (err) => {
								console.log('uploadImage fail', err);
								uni.showModal({
									content: err.errMsg,
									showCancel: false
								});
							}
						});
					},
					fail: (err) => {
						console.log('chooseImage fail', err)
						// #ifdef MP
						uni.getSetting({
							success: (res) => {
								let authStatus = res.authSetting['scope.album'];
								if (!authStatus) {
									uni.showModal({
										title: '授权失败',
										content: '需要从您的相册获取图片，请在设置界面打开相关权限',
										success: (res) => {
											if (res.confirm) {
												uni.openSetting()
											}
										}
									})
								}
							}
						})
						// #endif
					}
				})
			},
			radioChange(event){
				this.gender=event.detail.value
			},
			chooseType(type) {
				this.identityType  = type
			},
		},
		onUnload() {
			this.$common.removeLocal('identity')
		}
	}
</script>

<style lang="scss">
	.text-red {
		color: red;
	}
	.box {
		background: white;
		width: 100%;
		min-height: calc(100vh - var(--window-top) - var(--window-bottom));
		padding-bottom: 40rpx;
		
	}

	.tab-info {
		width: calc(100% - 60rpx);
		margin: 0 auto;
		display: flex;
		height: 100rpx;
		align-items: center;
		font-size: 30rpx;
		color: #666;

		.tab-li {
			height: 60rpx;
			line-height: 60rpx;
			margin-right: 30rpx;
			cursor: pointer;
		}

		.active {
			color: #333;
			font-weight: bold;
			font-size: 32rpx;
			border-bottom: 1px solid $color-primary;
		}
	}

	.next-btn {
		width: calc(100% - 60rpx);
		background: $color-primary;
		font-size: 30rpx;
		height: 90rpx;
		line-height: 90rpx;
		text-align: center;
		color: white;
		margin: 40rpx auto;
		border-radius: 10rpx;
	}

	.info-li {
		width: calc(100% - 60rpx);
		padding: 0 30rpx;
		min-height: 100rpx;
		align-items: center;
		display: flex;
		justify-content: space-between;
		font-size: 28rpx;
		border-bottom: 1px solid #eee;

		.left {
			display: flex;
			align-items: center;
			width: 220rpx;
			flex-shrink: 0;

			.iconfont {
				font-size: 40rpx;
				margin-right: 10rpx;
				flex-shrink: 0;
			}

			.title {
				width: 180rpx;
				flex-shrink: 0;
				font-size: 30rpx;
			}
		}

		.info-li-input {
			flex-grow: 1;
			text-align: right;
			font-size: 28rpx;
			height: 100rpx;
			line-height: 100rpx;
		}
	}

	.gender-l {
		margin-right: 18rpx;
	}

	.yzm-btn {
		padding: 12rpx 20rpx;
		margin-left: 10rpx;
		width: 160rpx;
	}


	.info-photo-li {
		flex-wrap: wrap;

		.card-title {
			width: 100%;
			height: 100rpx;
			line-height: 100rpx;
			font-size: 30rpx;
			font-weight: bold;
		}
	}

	.photo_list {
		width: 100%;
		height: auto;
		display: flex;
		margin-bottom: 40rpx;
		justify-content: space-between;

		.photo_li {
			width: 220rpx;
			height: 200rpx;
			background: #F2F2F2;
			border-radius: 7rpx;
			display: flex;
			align-items: center;
			justify-content: center;
			flex-wrap: wrap;
			position: relative;

			&:last-child {
				margin-right: 0;
			}

			.photo_default {
				display: flex;
				align-items: center;
				flex-wrap: wrap;
				justify-content: center;
				cursor: pointer;
			}

			.default_icon {
				width: 53rpx;
				height: 46rpx;
				text-align: center;
				margin: 0 auto;
			}

			.default_img {
				width: 220rpx;
				height: 200rpx;
			}

			.iconcuowu {
				position: absolute;
				top: -16rpx;
				right: -10rpx;
				background: #333;
				border-radius: 100%;
				width: 36rpx;
				height: 36rpx;
				z-index: 1;
				color: white;
				font-size: 30rpx;
				text-align: center;
				line-height: 36rpx
			}

			.default_text {
				width: 100%;
				text-align: center;
				font-size: 24rpx;
				margin-top: 16rpx;
				color: #666;
			}

			.img {
				width: 100%;
				height: 100%;
			}

			.close_icon {
				width: 32rpx;
				height: 32rpx;
				position: absolute;
				top: -10rpx;
				right: -10rpx;
				z-index: 2;
				cursor: pointer;
			}
		}

	}
</style>
